.about,
.contact,
.home {
  text-decoration: none;
  position: absolute;
  top: 0;
  left: 0;
  line-height: 24px;
  color: inherit;
  display: inline-block;
  width: 39px;
  height: 24px;
  z-index: 1;
}
.about,
.contact {
  left: 98px;
}
.contact {
  left: 196px;
  width: 50px;
}
.homeAboutContact {
  margin: 0;
  align-self: stretch;
  height: 24px;
  position: relative;
  text-align: left;
  font-size: var(--font-size-sm);
  color: var(--Colour-Main-Blue-500);
  font-family: var(--font-roboto);
}
.homeLink {
  margin: 0;
  width: 245px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-7xs) 0 0;
  box-sizing: border-box;
}
.rectangle {
  height: 60px;
  width: 1440px;
  position: relative;
  display: none;
  max-width: 100%;
}
.landie {
  text-decoration: none;
  position: relative;
  line-height: 38px;
  font-weight: 900;
  color: inherit;
  white-space: nowrap;
  z-index: 2;
}
.landingLink {
  width: 163px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 var(--padding-xl) 0 0;
  box-sizing: border-box;
}
.bg {
  height: 26px;
  width: 160px;
  position: relative;
  border-radius: var(--br-11xs);
  background-color: var(--Colour-Main-Blue-800);
  display: none;
}
.buttonLabel {
  text-decoration: none;
  flex: 1;
  position: relative;
  line-height: var(--font-size-lg);
  font-weight: 500;
  color: inherit;
  white-space: nowrap;
  z-index: 1;
}
.elementbuttonsmallfilled {
  align-self: stretch;
  border-radius: var(--br-11xs);
  background-color: var(--Colour-Main-Blue-800);
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-9xs) 0;
  white-space: nowrap;
  z-index: 1;
}
.nav,
.navigation,
.smallFilledButton {
  display: flex;
  align-items: flex-start;
  box-sizing: border-box;
}
.smallFilledButton {
  width: 160px;
  flex-direction: column;
  justify-content: flex-start;
  padding: var(--padding-7xs) 0 0;
  text-align: center;
  font-size: var(--font-size-xs);
  color: var(--Colour-Secondary-White-100-general);
}
.nav,
.navigation {
  flex-direction: row;
  max-width: 100%;
}
.nav {
  flex: 1;
  justify-content: center;
  padding: var(--padding-xs) var(--padding-xl) var(--padding-3xs);
  gap: var(--gap-252xl);
  z-index: 3;
  text-align: left;
  font-size: var(--font-size-7xl);
  color: var(--Colour-Main-Blue-600);
  font-family: var(--font-roboto);
}
.navigation {
  align-self: stretch;
  justify-content: flex-start;
  padding: 0 0 var(--padding-224xl);
}
@media screen and (max-width: 1200px) {
  .nav {
    gap: var(--gap-116xl);
  }
}
@media screen and (max-width: 750px) {
  .homeLink {
    display: none;
  }
  .nav {
    gap: var(--gap-49xl);
  }
}
@media screen and (max-width: 450px) {
  .nav {
    gap: var(--gap-15xl);
  }
}
