.div,
.rectangle {
  position: relative;
}
.rectangle {
  width: 1440px;
  height: 216px;
  background-color: var(--color-lavender);
  display: none;
  max-width: 100%;
}
.div {
  line-height: 24px;
  font-weight: 500;
  white-space: nowrap;
  z-index: 1;
}
.copyright {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-6xs) 0 0;
}
.landie {
  margin: 0;
  position: relative;
  font-size: inherit;
  line-height: 38px;
  font-weight: 900;
  font-family: inherit;
  z-index: 2;
}
.brandName {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 var(--padding-5xs) 0 0;
  font-size: var(--font-size-7xl);
  color: var(--Colour-Main-Blue-600);
}
.bg {
  height: 26px;
  width: 109px;
  position: relative;
  border-radius: var(--br-11xs);
  background-color: var(--Colour-Main-Blue-800);
  display: none;
}
.buttonLabel {
  flex: 1;
  position: relative;
  line-height: var(--font-size-lg);
  font-weight: 500;
  white-space: nowrap;
  z-index: 1;
}
.elementbuttonsmallfilled {
  align-self: stretch;
  border-radius: var(--br-11xs);
  background-color: var(--Colour-Main-Blue-800);
  flex-direction: row;
  justify-content: flex-start;
  padding: var(--padding-9xs) 0;
  white-space: nowrap;
  z-index: 1;
}
.elementbuttonsmallfilled,
.footerButton,
.footerContent {
  display: flex;
  align-items: flex-start;
}
.footerButton {
  width: 109px;
  flex-direction: column;
  justify-content: flex-start;
  padding: var(--padding-7xs) 0 0;
  box-sizing: border-box;
  text-align: center;
  font-size: var(--font-size-xs);
  color: var(--Colour-Secondary-White-100-general);
}
.footerContent {
  width: 1110px;
  flex-direction: row;
  justify-content: space-between;
  gap: var(--gap-xl);
  max-width: 100%;
}
.socialBackgroundShape {
  align-self: stretch;
  height: 1px;
  position: relative;
  background-color: var(--color-lightgray);
  z-index: 1;
}
.about,
.home {
  position: relative;
  line-height: 24px;
  z-index: 1;
}
.about {
  text-decoration: none;
  color: inherit;
}
.footerLinks {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-41xl);
}
.iconsocialinstagramCopy,
.iconsocialinstagramCopy4 {
  height: 16px;
  width: 16px;
  position: relative;
  min-height: 16px;
  z-index: 1;
}
.iconsocialinstagramCopy {
  object-fit: cover;
}
.instagramIcons,
.socialIcons {
  display: flex;
  align-items: flex-start;
}
.instagramIcons {
  align-self: stretch;
  flex-direction: row;
  justify-content: space-between;
  gap: var(--gap-xl);
}
.socialIcons {
  width: 200px;
  flex-direction: column;
  justify-content: flex-start;
  padding: var(--padding-9xs) 0 0;
  box-sizing: border-box;
}
.socialLinksContainer {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  gap: var(--gap-xl);
}
.footer,
.socialLinks {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  max-width: 100%;
}
.socialLinks {
  width: 1110px;
  align-items: flex-start;
  gap: var(--gap-xl);
  color: var(--Colour-Main-Blue-300);
}
.footer {
  align-self: stretch;
  background-color: var(--color-lavender);
  align-items: center;
  padding: var(--padding-42xl) var(--padding-xl) var(--padding-13xl);
  box-sizing: border-box;
  gap: var(--gap-21xl);
  z-index: 1;
  text-align: left;
  font-size: var(--font-size-sm);
  color: var(--Colour-Main-Grey-400);
  font-family: var(--font-roboto);
}
@media screen and (max-width: 750px) {
  .socialLinksContainer {
    flex-wrap: wrap;
  }
  .footer {
    gap: var(--gap-xl);
  }
}
@media screen and (max-width: 450px) {
  .landie {
    font-size: var(--font-size-2xl);
    line-height: var(--font-size-11xl);
  }
  .footerContent {
    flex-wrap: wrap;
    justify-content: center;
  }
}
