.mask,
.title {
  position: relative;
}
.mask {
  height: 742px;
  width: 1440px;
  display: none;
  max-width: 100%;
}
.title {
  margin: 0;
  align-self: stretch;
  font-size: inherit;
  line-height: 48px;
  font-weight: 500;
  font-family: inherit;
  z-index: 2;
}
.loremIpsumDolor {
  margin-block-start: 0;
  margin-block-end: 10px;
}
.musDonecQuam {
  margin: 0;
}
.body,
.infoCard {
  align-self: stretch;
}
.body {
  height: 140px;
  position: relative;
  font-size: var(--font-size-base);
  line-height: var(--font-size-7xl);
  color: var(--Colour-Main-Blue-400);
  display: inline-block;
  z-index: 2;
}
.infoCard {
  flex-direction: column;
  gap: var(--gap-base);
}
.infoCard,
.infoCardContent,
.infoCardContentWrapper,
.smallIconCards {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.smallIconCards {
  align-self: stretch;
  flex-direction: row;
  gap: var(--gap-11xl);
  font-size: var(--font-size-base);
}
.infoCardContent,
.infoCardContentWrapper {
  flex-direction: column;
}
.infoCardContent {
  align-self: stretch;
  gap: var(--gap-61xl);
}
.infoCardContentWrapper {
  flex: 1;
  padding: var(--padding-31xl-4) 0 0;
  box-sizing: border-box;
  min-width: 351px;
  max-width: 100%;
}
.undrawMobileLoginIkmvIcon {
  height: 524.3px;
  flex: 1;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  object-fit: cover;
  min-width: 351px;
  z-index: 2;
}
.undrawMobileLoginIkmvEle {
  align-self: stretch;
  background-color: var(--color-black);
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  flex-wrap: wrap;
  align-content: flex-start;
  padding: var(--padding-90xl-6) var(--padding-146xl) var(--padding-89xl-1);
  box-sizing: border-box;
  gap: var(--gap-11xl);
  max-width: 100%;
  z-index: 1;
  text-align: left;
  font-size: var(--font-size-17xl);
  color: var(--Colour-Main-Blue-900);
  font-family: var(--font-roboto);
}
@media screen and (max-width: 1050px) {
  .title {
    font-size: var(--font-size-10xl);
    line-height: 38px;
  }
}
@media screen and (max-width: 750px) {
  .smallIconCards {
    flex-wrap: wrap;
  }
  .infoCardContent {
    gap: var(--gap-21xl);
  }
  .infoCardContentWrapper {
    padding-top: var(--padding-14xl);
    box-sizing: border-box;
    min-width: 100%;
  }
  .undrawMobileLoginIkmvIcon {
    min-width: 100%;
  }
  .undrawMobileLoginIkmvEle {
    gap: var(--gap-mini);
    padding: var(--padding-52xl) var(--padding-63xl) var(--padding-51xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .title {
    font-size: var(--font-size-3xl);
    line-height: var(--font-size-10xl);
  }
  .infoCardContent {
    gap: var(--gap-xl);
  }
  .undrawMobileLoginIkmvEle {
    padding-left: var(--padding-xl);
    padding-right: var(--padding-xl);
    box-sizing: border-box;
  }
}
