@media screen and (max-width: 1200px) {
  .secondIntroCard {
    flex: 1;
  }
  .introCopy {
    gap: 62px;
    flex-wrap: wrap;
    padding-left: var(--padding-63xl);
    padding-right: var(--padding-63xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 1050px) {
  .title {
    font-size: var(--font-size-21xl);
    line-height: 53px;
  }
  .contentCardWrapper {
    padding-bottom: 251px;
    box-sizing: border-box;
  }
  .title1 {
    font-size: var(--font-size-10xl);
    line-height: 38px;
  }
  .secondIntroCard {
    min-width: 100%;
  }
}
@media screen and (max-width: 750px) {
  .contentCard {
    gap: var(--gap-15xl);
  }
  .undrawSocialInfluencerSgsvIcon {
    min-width: 100%;
  }
  .introCopy {
    gap: 31px;
    padding: 60px var(--padding-22xl) 59px;
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .title {
    font-size: var(--font-size-11xl);
    line-height: var(--font-size-21xl);
  }
  .largeButtons {
    gap: var(--gap-mini);
