.title {
  margin: 0;
  position: relative;
  font-size: inherit;
  line-height: 48px;
  font-weight: 500;
  font-family: inherit;
  z-index: 1;
}
.body,
.priceCard,
.title {
  align-self: stretch;
}
.body {
  height: 76px;
  position: relative;
  font-size: var(--font-size-base);
  line-height: var(--font-size-7xl);
  color: var(--Colour-Main-Blue-400);
  display: inline-block;
  z-index: 1;
}
.priceCard {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-base-3);
}
.price,
.title1 {
  align-self: stretch;
  position: relative;
}
.price {
  height: 62.4px;
  letter-spacing: 1px;
  line-height: 66px;
  font-weight: 500;
  display: inline-block;
  flex-shrink: 0;
  white-space: nowrap;
  z-index: 2;
}
.title1 {
  font-size: var(--font-size-base);
  line-height: var(--font-size-7xl);
  color: var(--Colour-Main-Blue-600);
  z-index: 1;
}
.priceDetails,
.priceValueCard {
  display: flex;
  align-items: flex-start;
  max-width: 100%;
}
.priceValueCard {
  width: 350px;
  flex-direction: column;
  justify-content: flex-start;
  gap: var(--gap-10xs-9);
}
.priceDetails {
  align-self: stretch;
  flex-direction: row;
  justify-content: center;
  padding: 0 var(--padding-xl) var(--padding-3xl-8);
  box-sizing: border-box;
  font-size: var(--font-size-31xl);
  color: var(--Colour-Main-Blue-700);
}
.bg,
.body1 {
  position: relative;
}
.body1 {
  align-self: stretch;
  line-height: 24px;
  z-index: 1;
}
.bg {
  height: 32px;
  width: 189px;
  border-radius: var(--br-11xs);
  background-color: var(--Colour-Main-Blue-800);
  display: none;
}
.buttonLabel {
  flex: 1;
  position: relative;
  line-height: 24px;
  font-weight: 500;
  white-space: nowrap;
  z-index: 1;
}
.elementbuttonmediumfilled {
  flex: 1;
  border-radius: var(--br-11xs);
  background-color: var(--Colour-Main-Blue-800);
  flex-direction: row;
  padding: var(--padding-9xs) 0 var(--padding-9xs-8);
  white-space: nowrap;
  z-index: 1;
}
.elementbuttonmediumfilled,
.priceButton,
.priceButtonContainer {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.priceButtonContainer {
  align-self: stretch;
  flex-direction: row;
  padding: 0 var(--padding-62xl) 0 var(--padding-61xl);
  color: var(--Colour-Secondary-White-100-general);
}
.priceButton {
  width: 350px;
  flex-direction: column;
  gap: var(--gap-3xs-1);
  max-width: 100%;
}
.priceButtonCard {
  align-self: stretch;
  flex-direction: row;
  justify-content: center;
  padding: 0 var(--padding-xl);
  box-sizing: border-box;
  font-size: var(--font-size-sm);
  color: var(--Colour-Main-Grey-600);
}
.priceButtonCard,
.pricingCard,
.pricingCardWrapper {
  display: flex;
  align-items: flex-start;
  max-width: 100%;
}
.pricingCard {
  width: 600px;
  flex-direction: column;
  justify-content: flex-start;
  gap: var(--gap-18xl-2);
}
.pricingCardWrapper {
  align-self: stretch;
  flex-direction: row;
  justify-content: center;
  padding: 0 var(--padding-xl) var(--padding-153xl-3);
  box-sizing: border-box;
  text-align: center;
  font-size: var(--font-size-17xl);
  color: var(--Colour-Main-Blue-900);
  font-family: var(--font-roboto);
}
@media screen and (max-width: 1050px) {
  .title {
    font-size: var(--font-size-10xl);
    line-height: 38px;
  }
  .price {
    font-size: var(--font-size-21xl);
    line-height: 53px;
  }
}
@media screen and (max-width: 750px) {
  .pricingCard {
    gap: var(--gap-lgi);
  }
  .pricingCardWrapper {
    padding-bottom: var(--padding-93xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .title {
    font-size: var(--font-size-3xl);
    line-height: var(--font-size-10xl);
  }
  .price {
    font-size: var(--font-size-11xl);
    line-height: var(--font-size-21xl);
  }
  .priceButtonContainer {
    padding-left: var(--padding-xl);
    padding-right: var(--padding-xl);
    box-sizing: border-box;
  }
}
