.callToActionShape {
  height: 100%;
  width: 100%;
  position: absolute;
  margin: 0 !important;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.undrawMention6k5dIcon {
  height: 312px;
  flex: 1;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  object-fit: cover;
  min-width: 289px;
  z-index: 1;
}
.title {
  margin: 0;
  align-self: stretch;
  position: relative;
  font-size: inherit;
  line-height: 48px;
  font-weight: 500;
  font-family: inherit;
  z-index: 1;
}
.loremIpsumDolor {
  margin-block-start: 0;
  margin-block-end: 10px;
}
.musDonecQuam {
  margin: 0;
}
.body {
  align-self: stretch;
  height: 166px;
  position: relative;
  font-size: var(--font-size-base);
  line-height: var(--font-size-7xl);
  color: var(--Colour-Main-Blue-400);
  display: inline-block;
  z-index: 1;
}
.actionTitleCard {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-base);
}
.bg {
  height: 32px;
  width: 150px;
  position: relative;
  border-radius: var(--br-11xs);
  background-color: var(--Colour-Main-Blue-800);
