}
.buttonLabel {
  flex: 1;
  position: relative;
  line-height: 24px;
  font-weight: 500;
  white-space: nowrap;
  flex-shrink: 0;
  z-index: 1;
}
.actionTitleCardParent,
.elementbuttonmediumfilled {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.elementbuttonmediumfilled {
  width: 150px;
  border-radius: var(--br-11xs);
  background-color: var(--Colour-Main-Blue-800);
  flex-direction: row;
  padding: var(--padding-9xs) 0;
  box-sizing: border-box;
  white-space: nowrap;
  z-index: 1;
  text-align: center;
  font-size: var(--font-size-sm);
  color: var(--Colour-Secondary-White-100-general);
}
.actionTitleCardParent {
  align-self: stretch;
  flex-direction: column;
  gap: var(--gap-21xl);
}
.callToAction,
.callToActionContent,
.cta {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  box-sizing: border-box;
  max-width: 100%;
}
.callToActionContent {
  flex: 1;
  flex-direction: column;
  padding: var(--padding-10xs-1) 0 0;
  min-width: 289px;
}
.callToAction,
.cta {
  flex-direction: row;
}
.cta {
  flex: 1;
  flex-wrap: wrap;
  align-content: flex-start;
  padding: var(--padding-98xl-9) var(--padding-150xl) var(--padding-97xl-1)
    var(--padding-190xl);
  position: relative;
  gap: var(--gap-153xl);
}
.callToAction {
  align-self: stretch;
  padding: 0 0 var(--padding-269xl-8);
  text-align: left;
  font-size: var(--font-size-17xl);
  color: var(--Colour-Main-Blue-900);
  font-family: var(--font-roboto);
}
@media screen and (max-width: 1200px) {
  .cta {
    gap: var(--gap-67xl);
    padding-left: var(--padding-85xl);
    padding-right: var(--padding-65xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 1050px) {
  .title {
    font-size: var(--font-size-10xl);
    line-height: 38px;
  }
  .callToAction {
    padding-bottom: var(--padding-169xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 750px) {
  .cta {
    gap: var(--gap-24xl);
    padding: var(--padding-58xl) var(--padding-23xl) var(--padding-56xl)
      var(--padding-33xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .title {
    font-size: var(--font-size-3xl);
    line-height: var(--font-size-10xl);
  }
  .actionTitleCardParent {
    gap: var(--gap-xl);
  }
  .callToAction,
  .cta {
    box-sizing: border-box;
  }
  .cta {
    gap: var(--gap-2xl);
    padding-left: var(--padding-xl);
    padding-right: var(--padding-xl);
  }
  .callToAction {
    padding-bottom: var(--padding-103xl);
  }
}
