.elementcardinfoCardsmalliIcon {
  height: 36px;
  max-width: 100%;
  overflow: hidden;
  flex-shrink: 0;
  object-fit: cover;
}
.body,
.elementcardinfoCardsmalliIcon,
.title {
  align-self: stretch;
  position: relative;
  z-index: 2;
}
.title {
  line-height: var(--font-size-7xl);
  font-weight: 500;
}
.body {
  font-size: var(--font-size-xs);
  line-height: var(--font-size-lg);
  color: var(--Colour-Main-Grey-600);
}
.smallIconCardContainer,
.smallIconCardDetails {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.smallIconCardDetails {
  align-self: stretch;
  gap: var(--gap-7xs);
}
.smallIconCardContainer {
  flex: 1;
  gap: var(--gap-sm);
  min-width: 166px;
  text-align: left;
  font-size: var(--font-size-base);
  color: var(--Colour-Main-Blue-900);
  font-family: var(--font-roboto);
}
